import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import linkState from 'linkstate';
import { route } from 'preact-router';


@observer
class Dogs extends Component {
    toggleDog(dogId) {
        const { object, type, elementtype, field, updateField, isNew } = this.props;
        const value = toJS(object[field]) || [];
        const idx = value.indexOf(dogId);
        if (idx > -1) {
            value.splice(idx, 1);
        } else {
            value.push(dogId);
        }
        updateField({ id: object.id, field, value, type, elementtype, isNew });
    }

    toggleFilter = () => {
        const { showAll } = this.state;
        this.setState({
            showAll: showAll ? 0 : 1,
        });
    }

    render() {
        const { showAll } = this.state;
        const { userStore, dogStore } = this.props.stores;
		const { dogs } = dogStore;
        const { saved = {}, object, field, title, icon, help, filter = () => true } = this.props;
        const value = toJS(object[field]) || [];
        const dogFilter = showAll ? () => true : filter;
        // console.log('Dogs.render: type, elementType, value:', type, elementtype, value);
        return (
            <div class={`form-group  mb-4 pb-1 border-bottom ${saved[`${field}.${object.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <div class='container-fluid px-0'>
                    <div class='row'>
                        {dogs && dogs.filter(dogFilter).map(dog => (
                            <div class='col-6 col-sm-6 col-md-6 col-lg-6 mb-1'>
                                <div class={`border rounded px-3 pt-2 pb-1 ${value.indexOf(dog.id) > -1 ? 'bg-success' : ''} d-flex`}
                                    onClick={() => this.toggleDog(dog.id)}
                                    style='padding: .3rem !important; line-height: 1.1em; cursor: pointer; pointer-events: inherit;'
                                >

                                    <div class={`text-center border rounded-circle imageRounded imageRoundedSmall`} style={`${dog.image ? `background-image: url("${dog.image.s3ThumbLink}");` : ''}`}>
                                        {!dog.image && <i class='fas fa-dog mt-1' style='font-size: 25px;' />}
                                    </div>
                                    <div class='flex-grow-1 pl-2 pr-1'
                                        style={`border-right: 4px solid ${userStore.findHarness(dog.harness, 'colorNonstop')} !important; line-height: 0.9em;`}
                                    >
                                        <small>
                                            {/* {dog.image ? <img src={dog.image.s3SmallLink} class='img-fluid rounded-circle mr-2' style='width: 25px; height: 25px;' /> : <i class='fas fa-dog mr-2' />} */}
                                            <small class='float-right'>
                                                {dog.birth && <span class='mr-1'>{util.age(dog.birth, dog.deceased)}</span>}
                                                {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                            </small>
                                            {dog.shortname || dog.name}<br />
                                            <div style='line-height: 0.8em;'><small>{userStore.findTeam(dog.team)}</small></div>
                                        </small>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <div class='row'>
                        <div class='col-12 text-right'>
                            {showAll ?
                                <button class='btn btn-sm btn-link' onClick={this.toggleFilter}>Vis kun hundene i teamet og de som er valgt</button> :
                                <button class='btn btn-sm btn-link' onClick={this.toggleFilter}>Vis alle hundene</button>
                            }
                        </div>
                    </div>
                </div>
                {saved[`${field}.${object.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

export default Dogs;
