import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import linkState from 'linkstate';

import PubSub, { topics } from '../../lib/pubsub';
import Images from '../../components/form/images';
import Dogs from '../../components/dog/dogs';

const initialState = {
	showLog: false,
	okToAddLog: true,
};

@observer
class DogAddLog extends Component {
	constructor(props) {
        super(props);
        this.state = {
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
                dogs: [],
			},
			...initialState,
        };
    }

	async loadDog(props = this.props) {
        const { dog } = props;
        if (dog && dog.id) {
            const { newHistory } = this.state;
            newHistory.dogs = [dog.id];
            this.setState({ newHistory });
        }
    }

    async loadFeed() {
		const { userStore } = this.props.stores;
        await userStore.getFeed();
    }

	toggleLog = () => {
		const { showLog } = this.state;
		this.setState({
            showLog: !showLog,
        });
	}

	resetLog = () => {
		this.setState({
			newHistory: {
				date: util.isoDate(new Date(), false, false, true),
				time: util.isoTime(new Date()),
                dogs: [],
			},
			showLog: false,
		});
	}

	addImages = (obj) => {
		// updateField({ id: object.id, field, value: file });
		// console.log('addImages', obj.value);
		if (Array.isArray(obj.value)) {
			const { newHistory } = this.state;
			if (Array.isArray(newHistory.images)) {
				newHistory.images = [...newHistory.images, ...obj.value];
			} else {
				newHistory.images = obj.value;
			}
			this.setState({ newHistory });
		}
	}

	handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

	imageUploadStatus = (status) => {
		this.setState({ okToAddLog: status });
	}

	insertLog = async () => {
		const { newHistory } = this.state;
		const { dogStore, userStore } = this.props.stores;
		const { dog } = dogStore;
		const { user } = userStore;

		if (!newHistory.title && !newHistory.comment) {
			PubSub.publish(topics.STATUS_MESSAGE, {
				message: 'Mangler tittel på logginnlegg',
				type: 'warning',
				icon: 'fas fa-exclamation-triangle',
			});
			return false;
		}

		if (!newHistory.id && newHistory.dogs.length === 0) {
			PubSub.publish(topics.STATUS_MESSAGE, {
				message: 'Mangler hunder til logginnlegg',
				type: 'warning',
				icon: 'fas fa-exclamation-triangle',
			});
			return false;
		}

		newHistory.date = `${newHistory.date} ${newHistory.time}`;
		await dogStore.save({
			id: dog.id || newHistory.dogs[0],
			history: {
				user: {
					id: user.id,
					email: user.email,
					firstname: user.firstname,
					lastname: user.lastname,
				},
				...newHistory,
			},
		});
        this.resetLog();
		this.loadDog();
        this.loadFeed();
	}


    updateField = ({ id, field, value, type, elementtype, isNew }) => {
        const { newHistory } = this.state;
        newHistory[field] = value;
        this.setState({ newHistory });
    }

    componentWillMount() {
        this.resetLog();
        this.loadDog();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.dog.id !== this.props.dog.id) {
            this.resetLog();
            this.loadDog(nextProps);
        }
    }

	render() {
		const { newHistory, showLog, okToAddLog } = this.state;
		const { dog } = this.props;

		if (showLog) {
			return (
				<div class='col-12 px-0'>
					<button class='btn btn-sm btn-secondary float-left'
						onClick={this.toggleLog}
					>
						<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
					</button>
					<br />

					{showLog && <div class='border-bottom mb-2 py-4 px-5'>
						<h5>Legg til logg</h5>

                        <Dogs stores={this.props.stores} object={newHistory} type='array' elementtype='number' field='dogs' icon='fas fa-dog' title='Hunder' help='Hvilke hunder gjelder dette?' updateField={this.updateField} filter={(e) => dog.id === e.id || newHistory.dogs.indexOf(e.id) > -1} />

						<div class='row'>
							<div class='form-group col'>
								<label for='dateInput'><i class='fas fa-calendar-alt' /> Dato</label>
								<input type='date' class='form-control' id='dateInput' aria-describedby='dateHelp'
									onInput={linkState(this, 'newHistory.date')}
									value={newHistory.date}
									autocomplete='off'
								/>
								<small id='dateHelp' class='form-text text-muted'>Dato for logglinje.</small>
							</div>
							<div class='form-group col'>
								<label for='timeInput'><i class='fas fa-clock' /> Klokka</label>
								<input type='time' class='form-control' id='timeInput' aria-describedby='timeHelp'
									onInput={linkState(this, 'newHistory.time')}
									value={newHistory.time}
									autocomplete='off'
								/>
								<small id='timeHelp' class='form-text text-muted'>Tid for logglinje.</small>
							</div>
						</div>
						<div class='form-group'>
							<label for='nameInput'>Tittel</label>
							<input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
								onInput={linkState(this, 'newHistory.title')}
								value={newHistory.title}
								autocomplete='off'
								required={true}
							/>
							<small id='nameHelp' class='form-text text-muted'>Tittel på logglinje. Vetrinær besøk, Vaksinering, Syk, Profilbilde, etc.</small>
						</div>
						<div class='form-group'>
							<label for='typeInput'><i class='fas fa-folder-open' /> Type hendelse</label>
							<select class='form-control' id='typeInput' aria-describedby='typeHelp'
								onInput={linkState(this, 'newHistory.historyType')}
								value={newHistory.historyType}
								autocomplete='off'
							>
								<option value=''>-- Velg type --</option>
								<option value='other'>Annet</option>
								<option value='clawClip'>Kloklipp</option>
								<option value='maturity'>Løpetid</option>
								<option value='newStraw'>Ny halm/treull</option>
								<option value='update'>Oppdatering</option>
								<option value='mating'>Parring</option>
								<option value='injury'>Skade</option>
								<option value='fight'>Slåsskamp</option>
								<option value='sick'>Syk</option>
								<option value='vaccine'>Vaksine</option>
								<option value='weight'>Veiing</option>
								<option value='veterinarianVisit'>Vetrinær besøk</option>
							</select>
							<small id='nameHelp' class='form-text text-muted'>Type hendelse. Vetrinær besøk, vaksinering, syk, løpetid, etc.</small>
						</div>
						{newHistory.historyType === 'weight' && <div class='form-group'>
							<label for='weightInput'><i class='fas fa-balance-scale' /> Vekt</label>
							<input type='text' class='form-control' id='weightInput' aria-describedby='weightHelp'
								onInput={linkState(this, 'newHistory.weight')}
								value={newHistory.weight}
								autocomplete='off'
							/>
							<small id='weightHelp' class='form-text text-muted'>Vekten til hunden.</small>
						</div>}
						<div class='form-group'>
							<label for='commentInput'><i class='fas fa-quote-right' /> Innhold</label>
							<input type='text' class='form-control' id='commentInput' aria-describedby='commentHelp'
								onInput={linkState(this, 'newHistory.comment')}
								value={newHistory.comment}
								autocomplete='off'
							/>
							<small id='commentHelp' class='form-text text-muted'>Innholdet til loggen. Skriv mer om hva som hendte.</small>
						</div>
						{(newHistory.historyType === 'veterinarianVisit' || newHistory.historyType === 'vaccine') && <div class='form-group'>
							<label for='costInput'><i class='fas fa-money-bill-alt' /> Kostnad</label>
							<input type='text' class='form-control' id='costInput' aria-describedby='costHelp'
								onInput={linkState(this, 'newHistory.cost')}
								value={newHistory.cost}
								autocomplete='off'
								pattern='[0-9]{3}' title='Kun tall. Ikke noen andre tegn eller symboler.'
							/>
							<small id='costHelp' class='form-text text-muted'>Kostnad for denne hendelsen.</small>
						</div>}

						<div class='container-fluid'>
							<div class='row'>
								<div class='row-12'>
									<label for='imageInput'><i class='fas fa-camera' /> Bilder</label>
								</div>
							</div>
							<div class='row'>
								{newHistory.images && newHistory.images.map(img => (
									<div class='col-3 p-1'>
										<img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
									</div>
								))}
							</div>
						</div>
						<Images object={newHistory} field='images' updateField={this.addImages} uploadStatus={this.imageUploadStatus} />

						<button type='button' class='btn btn-success'
							onClick={this.insertLog}
							disabled={!okToAddLog}
						>
							{okToAddLog ? 'Legg til logg' : 'vent litt...'}
						</button>
						<button type='button' class='btn btn-danger float-right'
							onClick={this.resetLog}
						>
							Glem det
						</button>
					</div>}

				</div>
			);
		}

		return (
			<button class='btn btn-sm btn-secondary float-left'
				onClick={this.toggleLog}
			>
				<i class='fas fa-plus' /> {showLog ? 'Skjul logg' : 'Ny logg'}
			</button>
		);
	}
}

export default DogAddLog;
